import { useState, useEffect } from 'react';
import LazyLoad from 'vanilla-lazyload';
import lazyloadConfig from './config/lazyioad';
import { useMediaQuery } from '@react-hook/media-query';

if (!document.lazyLoadInstance) {
  document.lazyLoadInstance = new LazyLoad(lazyloadConfig);
}

const LazyImage = ({ portfolioImg, img, isHidden }) => {
  const [imgHidden, setImgHidden] = useState(true);

  const isMobile = useMediaQuery('only screen and (max-width: 767px)');
  const isTablet = useMediaQuery(
    'only screen and (min-width: 768px) and (max-width: 1199px)'
  );
  const isDesktop = useMediaQuery('only screen and (min-width: 1200px)');

  const {
    mobileBase450px,
    alt,
    mobile1x,
    tablet1x,
    desktop1x,
    mobile2x,
    tablet2x,
    desktop2x,
  } = img;

  useEffect(() => {
    document.lazyLoadInstance.update();
    setImgHidden(false);
  }, []);

  const imageDevice = () => {
    if (isMobile) {
      return mobile1x;
    }
    if (isTablet) {
      return tablet1x;
    }
    if (isDesktop) {
      return desktop1x;
    }
  };

  const baseImage = () => {
    if (isMobile) {
      return mobileBase450px;
    }
    if (isTablet) {
      return 'data:image/gif;base64,R0lGODlhOwAxAIAAAP///wAAACH5BAEAAAEALAAAAAA7ADEAAAI4jI+py+0Po5y02ouz3rz7D4biSJbmiabqyrbuC8fyTNf2jef6zvf+DwwKh8Si8YhMKpfMpvPZKgAAOw==';
    }
    if (isDesktop) {
      return 'data:image/gif;base64,';
    }
  };

  return (
    <picture>
      <source
        media="(min-width: 1200px)"
        data-srcset={`${desktop1x} 1x, ${desktop2x} 2x`}
      />
      <source
        media="(min-width: 768px)"
        data-srcset={`${tablet1x} 1x, ${tablet2x} 2x`}
      />
      <source
        media="(max-width: 767px)"
        data-srcset={`${mobile1x} 1x, ${mobile2x} 2x`}
      />

      <img
        className={`${'lazy'} ${portfolioImg} ${imgHidden ? isHidden : ''}`}
        src={baseImage()}
        data-src={imageDevice()}
        alt={alt}
      />
    </picture>
  );
};

export default LazyImage;
